body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  background-image: url('background.jpg');
  background-size: cover;
  background-position: center;
  font-family: 'Poppins', sans-serif;
}

.start-screen {
  text-align: center;
  margin-top: -20%;
}

.start-screen button {
  font-size: 1.5rem;
  padding: 15px 30px;
  background-color: #4CAF50;
  color: white;
  border: 3px solid #4CAF50;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 0 0 10px rgba(76, 175, 80, 0.8);
}

.start-screen button:hover {
  background-color: #45a049;
  transform: scale(1.05);
}

.board {
  display: grid;
  grid-template-columns: repeat(10, 40px);
  grid-gap: 6px;
}

.cell {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
}

.auth-container {
  max-width: 400px;
  margin: 100px auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fff;
}

.auth-container h2 {
  text-align: center;
}

.auth-container form {
  display: flex;
  flex-direction: column;
}

.auth-container input {
  margin-bottom: 10px;
  padding: 8px;
}

.auth-container button {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
}

.auth-container .error {
  color: red;
  text-align: center;
}

.auth-container p {
  text-align: center;
  color: #007bff;
  cursor: pointer;
}

