.auth-container {
    width: 350px;
    margin: 100px auto;
    padding: 30px;
    background: rgba(255, 255, 255, 0.9);
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.auth-container h2 {
    margin-bottom: 20px;
    font-size: 24px;
}

.auth-container input {
    width: 90%;
    margin: 10px 0;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.auth-container button {
    width: 95%;
    padding: 12px;
    margin-top: 10px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

.auth-container button:hover {
    background-color: #45a049;
}

.error-message {
    color: red;
    margin-top: 15px;
}

.toggle-link {
    color: #2196f3;
    cursor: pointer;
    text-decoration: underline;
}

.toggle-link:hover {
    color: #1769aa;
}
